  .carousel-caption{
		width: 25%;
    top: 30% !important;
		left: 200 !important;
		font-size: 22px;
    color: rebeccapurple !important
	}
	
	.carousalContainer {
		position: relative;
	}
	.cover {
		background: #2ec8a6;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		position: absolute;
		opacity: 0.25;
	}
  .descCarousal {
		width: 35%;
		top: 26%;
		left: 15%;
		text-align: center;
		position: absolute;
		font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
		font-size: 25px;
	}
 

	.descCarousal .btnStartNow {
		 background: #2ec8a6;
		 color: #ffffff;
		 font-weight: bold;
		 font-size: 20px;
		 padding-left: 20px;
		 padding-right: 20px;
		 border-radius: 50px ;
	}

